<template>
  <div class="home">
    <!-- Loading indicators -->
    <div v-if="notesLoading" class="note-grid">
      <LoadingNoteCard v-for="(card, index) in 10" :key="index"  />
    </div>

    <!-- Error Message -->
    <div v-else-if="notesError" class="notes-loading-error">
      <h1>Server Error!</h1>
      <h3>Please Try Again Later!</h3>
    </div>

    <!-- Notes -->
    <NoteGrid v-else-if="notes" :notes="notes" />

    <!-- If there is no note -->
    <div v-else><h1>There's no note in the system.</h1></div>

    <!-- Load More Notes Button -->
    <button v-if="moreNoteButtonShow" @click="loadMoreNotes" class="more-button">
      <span class="material-icons">more_horiz</span>
    </button>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import NoteGrid from '@/components/NoteGrid';
import LoadingNoteCard from '@/components/LoadingNoteCard';

export default {
  name: 'Home',
  components: {
    NoteGrid,
    LoadingNoteCard
  },
  computed: {
    ...mapState([
      'notes',
      'notesError',
      'notesLoading',
      'moreNoteButtonShow'
    ]),
  },
  mounted() {
    // Load notes and limit by 1 page
    this.fetchNotes(1);
  },
  methods: {
    ...mapActions([
      'fetchNotes'
    ]),
    loadMoreNotes() {
      // Next page (offset = page * limit)
      this.$store.dispatch('notesNextPage');
      // Fetch notes and add note list
      this.$store.dispatch('fetchAndAddToNotes');
    }
  },
}
</script>
